import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormControlBase } from '../../form-controls/form-control-base';
import { FormControlService } from '../../form-controls/form-control.service';

@Component({
  selector: 'angular-form-builder',
  template: `<mat-card>
  <mat-card-header>
    <mat-card-title>
      <b>{{title}}</b>
    </mat-card-title>
  </mat-card-header>
  <form (ngSubmit)="formSubmit.emit(form)" [formGroup]="form">
    <mat-card-content>
      <div *ngFor="let question of questions">
        <app-question [question]="question" [form]="form"></app-question>
      </div>
    </mat-card-content>
    <mat-card-actions>
      <button id=submit type="submit" mat-button [disabled]="!form.valid || disableButtons">SAVE</button>
      <button [disabled]="disableButtons" type="button" mat-button (click)="onReset()">RESET</button>
      <ng-content></ng-content>
    </mat-card-actions>
  </form>
</mat-card>`,
  providers: [FormControlService],
})
export class FormBuilderComponent implements OnInit {
  @Input() title: string;
  @Input() questions: FormControlBase<any>[] = [];
  @Input() disableButtons = false;
  @Output() formSubmit = new EventEmitter<any>();
  @Output() formReset = new EventEmitter<any>();

  form: FormGroup;

  constructor(private qcs: FormControlService) {}

  ngOnInit() {
    this.form = this.qcs.toFormGroup(this.questions);
  }

  onReset() {
    this.form.reset();
    this.form = this.qcs.toFormGroup(this.questions);
    this.formReset.emit(this.form);
  }
}
